.profile {
    text-align: center;
}

.profile img {
    border: 10px solid #bdbdbd;
    border-radius: 50%;
    max-width: 500px;
}

.profile h2,
.profile p {
    text-align: center;
    color: #2e2c2d;
}
.repo {
    background-color: #fbfbfb;
    padding: 10px;
    margin-top: 10px;
    border-radius: 10px;
}

.repo:first-of-type {
    margin-top: 0px;
}

.repo-header {
    display: flex;
}

.repo-header a {
    color: #2e2c2d;
}

.repo-header img {
    width: 32px;
    margin-right: 10px;
}


.links ul {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}

.links li {
    max-width: 128px;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
}

body {
    background-color: #e7e7e7;
}

#root {
    margin: 0 auto;
    justify-content: center;
    max-width: 1200px;
}

img {
    width: 100%;
}

h1 {
    font-size: 48px;
    color: #b21a21;
    border-bottom: 1px solid #b21a21;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 24px;
}

p, a, li {
    font-size: 18px;
}

section h1 {
    text-align: center;
}

section {
    padding: 10px;    
}
